//other import
import React from 'react';
//styles
//my components
import {PersonalDataInput} from '../../../../commonComponents/PersonalDataInput/PersonalDataInput';

interface IPropsTypes {
    name: string
    phoneNumber: string
    homeAddress: string
    changeName: (name: string) => void
    changePhoneNumber: (phoneNumber: string) => void
    changeHomeAddress: (homeAddress: string) => void
}

export const CourierForm:React.FC<IPropsTypes> = ({name, phoneNumber, homeAddress, changeName, changePhoneNumber, changeHomeAddress}) => {
    return (
        <>
            <PersonalDataInput changeValue={changeName} value={name} title='   ?' placeholder="  '"/>
            <PersonalDataInput type='number' changeValue={changePhoneNumber} value={phoneNumber} title=' ?' placeholder="  "/>
            <PersonalDataInput changeValue={changeHomeAddress} value={homeAddress} title=' ' placeholder="   "/>
        </>
    )
}