//other import
import React from 'react';
//styles
//my components
import {PersonalDataInput} from '../../../../commonComponents/PersonalDataInput/PersonalDataInput';

interface IPropsTypes {
    name: string
    phoneNumber: string
    changeName: (name: string) => void
    changePhoneNumber: (phoneNumber: string) => void
}

export const SelfPickupForm:React.FC<IPropsTypes> = ({name, phoneNumber, changeName, changePhoneNumber}) => {
    return (
        <>
            <PersonalDataInput changeValue={changeName} value={name} title='   ?' placeholder="  '"/>
            <PersonalDataInput type='number' changeValue={changePhoneNumber} value={phoneNumber} title=' ?' placeholder="  "/>
        </>
    )
}